.airdrop-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background: url('bg/bag2.webp') no-repeat center center fixed;
  background-size: cover; /* Изображение будет масштабироваться и покрывать всю область */
  background-position: center; /* Центрируем изображение */
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  padding: 20px;
}


.info-section {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 20px; /* Увеличиваем радиус для более плавных углов */
  padding: 30px; /* Увеличиваем отступы */
  width: 90%;
  max-width: 600px; /* Увеличиваем максимальную ширину */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5); /* Увеличиваем тень */
  margin-bottom: 20px; /* Добавляем отступ снизу */
  position: relative;
  top: -20px; /* Поднимаем блок выше */
  display: flex;
  flex-direction: column;
  align-items: center; /* Центрируем содержимое по горизонтали */
}

.info-section p {
  margin: 0;
  font-size: 14px; /* Увеличиваем размер шрифта */
  color: #fff; /* Изменяем цвет текста */
}

.plantation {
  width: 100px;
  height: 100px;
  background: url('plant.png') no-repeat center center;
  background-size: contain;
  border: none;
  border-radius: 50%; /* Делаем изображение круглым */
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.5), 0 0 30px rgba(255, 215, 0, 0.5), 0 0 40px rgba(255, 69, 0, 0.5); /* Добавляем тени */
  margin-top: 20px; /* Отступ сверху */
  transition: transform 0.3s;
}

.plantation:hover {
  transform: scale(1.1);
}
