.earn-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  width: 100vw;
  background: url('background_earn.jpg') no-repeat center center fixed;
  background-size: cover;
  color: white;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: scroll;
  overflow-x: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.earn-page::-webkit-scrollbar {
  display: none;
}

.earn-info {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  padding: 15px 20px;
  margin-bottom: 20px;
  margin-top: 80px;
  width: 90%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  font-size: 16px;
  color: #fff;
}

.case-container-wrapper {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  width: 90%;
  margin-bottom: 100px;
}

.case-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.case {
  width: 90px;
  height: 90px;
  background-size: cover;
  background-position: center;
  border: none;
  border-radius: 15px;
  position: relative;
  transition: transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.case:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}

.case-cooldown {
  filter: grayscale(100%);
  pointer-events: none;
}

.case-details-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  color: white;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}

.case-details {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
}

.case-details img {
  width: 90px;
  height: 90px;
  border-radius: 15px;
  border: 2px solid #fff;
  padding: 5px;
  background: #000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.case-details button {
  margin-top: 10px;
  padding: 12px 25px;
  background: linear-gradient(135deg, #FFD700, #FFA500);
  color: black;
  border: none;
  font-size: 16px;
  font-weight: bold;
  border-radius: 25px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.case-details button:hover {
  background: linear-gradient(135deg, #FFA500, #FFD700);
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}

.cooldown-timer {
  position: absolute;
  top: 5px;
  left: 5px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 2px 5px;
  border-radius: 5px;
  font-size: 12px;
}

.case-cost {
  position: absolute;
  bottom: 5px;
  right: 5px;
  background: rgba(255, 215, 0, 0.8);
  color: black;
  padding: 2px 5px;
  border-radius: 12%;
  font-size: 12px;
  display: flex;
  align-items: center;
}

.coin-icon {
  width: 12px;
  height: 12px;
  margin-right: 2px;
}

/* Новые стили для анимации прокрутки */

.case-opening-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  color: white;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}

/* Стиль для указателя */
.cursor {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 10px; /* Сделаем указатель шире */
  height: 100px;
  background: red; /* Сделаем указатель красного цвета для лучшей видимости */
  z-index: 1001;
  border-radius: 2px;
}


/* Устанавливаем новые стили для контейнера и элементов прокрутки */
.scroll-container {
  width: 100%; /* Делаем контейнер шире, чтобы вместить больше элементов */
  height: 100px;
  overflow: hidden;
  position: relative;
}

.scroll-items {
  display: flex;
  animation: scroll 5s cubic-bezier(0.42, 0, 0.58, 1);
  position: relative; /* Добавляем позиционирование */
}

.scroll-item {
  width: 100px;
  height: 100px;
  background: #fff;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  margin-right: 10px;
  font-size: 24px;
  font-weight: bold;
  transition: transform 0.3s;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-100px * 50)); /* Количество элементов * ширина + марджин */
  }
}


.reward-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  color: white;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}

.reward-details {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
}
